import { Divider, Typography, notification } from 'antd';
import React from 'react';
import { Button, Input, Col, Row} from 'antd';

import CryptoJS from 'crypto-js';

const { Title, Text } = Typography;

// 
function prepareSecret(secret){
    if (!secret) {
        secret = 'secret';
    }
    if (secret.length < 32) {
        secret = CryptoJS.MD5(secret).toString()
    }
    return secret;
}

// 
function encryptData(data, secret) {
    return CryptoJS.AES.encrypt(data, prepareSecret(secret)).toString();
}

// 
function dencryptData(data, secret) {
    return CryptoJS.AES.decrypt(data, prepareSecret(secret)).toString(CryptoJS.enc.Utf8);
}

// 
function checkData(data, secret) {
    if (!data) {
        notification.error({
            description: '',
            placement: 'top'
        });
        return false;
    }
    if (!secret) {
        notification.info({
            description: ', ',
            placement: 'top'
        });
    }
    return true;
}

const handleEncryptAES = (data, secret, writeResult) => {
    if(!checkData(data, secret)) {
        return;
    }
    writeResult(encryptData(data, secret))
}

const handleDencryptAES = (data, secret, writeResult) => {
    if(!checkData(data, secret)) {
        return;
    }
    try {
        const result = dencryptData(data, secret);
        if (result === '') {
            notification.error({
                description: ', ',
                placement: 'top'
            });
        }
        writeResult(result)
    } catch (e) {
        notification.error({
            description: ', , : ' + e.message,
            placement: 'top'
        });
        writeResult('')
    }
}

const Encrypt = () => {

    const [inputData, setInputData] = React.useState('');
    const [outputData, setOutputData] = React.useState('');
    const [secret, setSecret] = React.useState('');

    return (<>
        <Title level={3}>/</Title>
        <Divider />

        <Row style={{marginTop: '10px'}}>
            <Text>(UTF8)</Text>
            <Input.TextArea style={{marginTop: '5px'}} rows={8} value={inputData} onChange={e => setInputData(e.target.value)}/>
        </Row>
        <Row style={{marginTop: '10px'}}>
            <Col span={2}><Button type="primary" onClick={e => handleEncryptAES(inputData, secret, setOutputData)}>AES</Button></Col>
            <Col span={2}><Button type="primary" onClick={e => handleDencryptAES(inputData, secret, setOutputData)}>AES</Button></Col>
            <Col span={6}><Input placeholder="" value={secret} onChange={e => setSecret(e.target.value)}/></Col>
        </Row>
        

        <Row style={{marginTop: '10px'}}>
            <Text></Text>
            <Input.TextArea style={{marginTop: '5px'}} rows={8} value={outputData} />
        </Row>

    </>);
}
export default Encrypt;